import React, { Component } from 'react';

import { Button, Icon, Col, List, Row, Badge, notification, Drawer  } from 'antd';

const openNotification = () => {
    notification.open({
        message: 'Astuce',
        description: "Lorsque vous faites vos courses, il suffit de cliquer sur un produit pour qu'il soit \"barré\" et \"grisé\".",
        duration: 0,
        style: {
            textAlign: 'justify'
          },
        onClose: () => {
            notification.open({
                message: 'Astuce',
                description: "Vous ouvez rajouter un produit de dernière minute en l'ajoutant dans la case \"Entrez un produit\".",
                duration: 0,
                style: {
                    textAlign: 'justify'
                  },
              });
            }
      });
  };
let countItem = 0;

class EnCourseList extends Component {
    constructor(props) {
        super(props);
        this.state = { 
            count: 0,
            arrayData: []
         }
    }

    componentDidMount() {
        if(this.props.help) {
            openNotification();
        }
        this.renderData();
        window.addEventListener('scroll', this.props.handleScroll);
    }

    componentDidUpdate(prevProps, prevState) {
        if(prevProps.data !== this.props.data) {
            this.renderData();
        }
    }

    componentWillUnmount() {
        window.removeEventListener('scroll', this.props.handleScroll);
    }

    //compteur de produits
    countProduit = ( data, currentTitre ) => {
        const arrayData = [];
        if(!data) {
            return null
        }
        Object.keys(data)
            .map(item => item === currentTitre ? Object.keys(data[item])
                .map(category => Object.keys(data[item][category])
                    .map(categoryItem => data[item][category][categoryItem].categoryItems && data[item][category][categoryItem].categoryIsVisible ? arrayData.push(data[item][category][categoryItem]): null))
            : null);
        let countItemPos = [];
        let countItemNeg = [];
        let countTotal = [];
        arrayData.map(elt => Object.keys(elt.categoryItems).map(item => countTotal.push(elt.categoryItems[item].checked)));
        arrayData.map(elt => Object.keys(elt.categoryItems).map(item => elt.categoryItems[item].checked === false && elt.categoryItems[item].itemIsVisible ? countItemNeg.push(elt.categoryItems[item].checked): countItemPos.push(elt.categoryItems[item].checked)));
        countItem = countTotal.length - countItemPos.length;
    }

    renderData = () => {
        const { data, currentTitre, inverseProduit } = this.props;
        const arrayData = [];
        if(!data) {
            return null
        }
        Object.keys(data)
            .map(item => item === currentTitre ? 
                Object.keys(data[item])
                    .map(category => Object.keys(data[item][category])
                        .map(categoryItem => arrayData.push(data[item][category][categoryItem])))
                : null);
        if(inverseProduit) {
            arrayData.reverse();
        }
        this.setState({ arrayData });
    }

    // création de la liste de produit et des catégories
    liste = () => {
        const { arrayData } = this.state;
        if(!arrayData) {
            return null
        }
        
        return (arrayData || []).map((elt, id) =>
        <div key={`key-${id}`}>
        {
            elt.categoryIsVisible ? // cache ou affiche une catégorie
            <List
                key={`titreCat-${elt.titreCat}`}
                id={`titreCat-${elt.titreCat}`}
                header={
                    <Row id={`titreCat-${id}`} type="flex" justify="space-between" align='middle' style={{height: '2.5em'}}>
                        <Col span={24} className='titreCat' onClick={() => this.props.checkCurrentCategory(elt.titreCat)}>
                            {elt.titreCat}
                        </Col>
                    </Row>
                }
                bordered
                dataSource={Object.keys(elt.categoryItems || []).map(item => elt.categoryItems[item]).reverse()}
                renderItem={(item, id) => (
                    <>
                    {
                    item.itemIsVisible ? //cache ou affiche un produit
                    <List.Item key={`listItemId-${item.key}`} id={elt.titreCat} className={item.checked ? '.ant-list-item flip' : '.ant-list-item'}>
                            <Col span={24} key={`col24-${item.key}`}>
                                <Row 
                                    type="flex" 
                                    justify="space-between" 
                                    align="middle"
                                    key={`elt-${item.key}`}
                                    id={`elt-${item.key}`}
                                >
                                    <Col xs={20} sm={21} md={21} lg={22} key={`col20-${item.key}`} onClick={() => this.props.isChecked(item, arrayData)} className={'ant-list-items-item'}>
                                        {item.item}
                                    </Col>
                                    <Col xs={4} sm={3} md={3} lg={2} key={`col4-${item.key}`}>
                                            <Button 
                                                name="comment" 
                                                className='comment' 
                                                style={{ background: 'transparent', border: 'none' }}
                                                type='primary'
                                                block
                                                onClick={() => this.props.toggleComment(item)}>
                                                {
                                                    item.comment === '' && item.count === 0 ?
                                                    <Icon type='message' style={{color: '#009688' , fontSize: '1.4em' }}/>
                                                    :
                                                    <Badge status="success">
                                                        <Icon type='message' style={{color: '#009688' , fontSize: '1.4em' }}/>
                                                    </Badge>
                                                }
                                            </Button>
                                    </Col>
                                </Row>
                            </Col>
                    </List.Item>
                    :
                    null
                    }
                    </>
                    )}
            />
            :
            null
        }
        </div>
        );
    }
    render() { 
        const { item, largeur, visible, showForm, currentTitre, data } = this.props;
        // width du drawer en fonction de la taille de l'écran
        const drawerWidth = largeur > 720 ? '50%' : '90%';
        const positionY = window.scrollY;
        this.countProduit(data, currentTitre);
        return ( 
            <Col onScroll={ e => this.props.handleScroll(e) } className='containerList'>
                    <Col xs={{ span: 24 }} md={{ span: 22, push: 1 }} lg={{ span: 20, push: 2 }} id='containerForm'>
                    {
                        showForm ?
                        <>
                            <Col xs={{ span: 12}} sm={{ span: 10}} md={{ span: 8}} lg={{ span: 6}}>
                                <h3><i className="far fa-list-alt" style={{ color: '#078073', padding: '0.5em'}}></i>{currentTitre}</h3>
                            </Col>
                            
                            <form id='myForm' className={showForm && positionY <= 80 ? 'formu formu-titre enCourse-formu-titre' : 'formu formu-titre enCourse-formu-titre fixedPosition'} onSubmit={this.props.createNewItemLastMinute}>
                                <input type='text' placeholder='Produit de dernière minute' maxLength={40} />
                                <button type='submit' id='buttonSubmit' onClick={this.props.showFormulaire}>
                                    <Badge count={countItem} offset={[11, -30]} style={{ backgroundColor: '#fff', color: '#999', boxShadow: '0 0 0 1px #d9d9d9 inset' }}>
                                        <Icon type="plus" />
                                    </Badge>
                                </button>
                            </form>
                        </>
                        :
                        <>
                            <Col xs={{ span: 12}} sm={{ span: 10}} md={{ span: 8}} lg={{ span: 6}}>
                                <h3><i className="far fa-list-alt" style={{ color: '#078073', padding: '0.5em'}}></i>{currentTitre}</h3>
                            </Col>
                            <Col xs={{ span: 12}} sm={{ span: 14}} md={{ span: 16}} lg={{ span: 18}}>
                                <button type='button' className={!showForm && positionY <= 80 ? '' : 'fixedPosition'} id='buttonSubmit2' onClick={this.props.showFormulaire} >
                                    <Badge count={countItem} offset={[5, -5]} style={{ backgroundColor: '#fff', color: '#999', boxShadow: '0 0 0 1px #d9d9d9 inset' }}>
                                        <Icon type="edit" style={{ fontSize: '2.5em' }}/>
                                    </Badge>
                                </button>
                            </Col>
                        </>
                    }
                    </Col>
               <Col span={24}>
                    {this.liste()}
                </Col>
                <Col span={6} style={{ marginRight: '1em' }}>
                    <Button block type="primary" onClick={this.props.goHome} className='goBack'>
                        <Icon type="left" />Retour
                    </Button>
                </Col>
                <Drawer
                    placement="right"
                    width={drawerWidth}
                    onClose={(e) => this.props.onClose(e, item)}
                    visible={this.props.visible}
                    >
                    {
                        visible ?
                        this.props.modalContentCourse(item)
                        :
                        null
                    }
                </Drawer>
            </Col>
         );
    }
}
 
export default EnCourseList;