import React, { Component } from 'react';

import { Button, Icon, Row, Col, notification, Switch } from 'antd';

const openNotification = () => {
    notification.open({
        message: 'Votre profil',
        description: "Bientôt vous aurez la possibilité de modifier votre profil et bien plus encore!",
        duration: 0,
        style: {
            textAlign: 'justify'
          },
        onClose: () => {
            notification.open({
                message: 'Se connecter à un autre profil',
                description: "Il vous suffit de recopier l'identifiant de la liste que vous souhaitez rejoindre et de valider. Ensuite cliquez sur cette liste pour la rejoindre!",
                duration: 0,
                style: {
                    textAlign: 'justify'
                  },
                // onClose: () => {
                //   alert('clicked');
                // },
              });
        },
      });
  };

class ProfilUser extends Component {
    componentDidMount() {
        if(this.props.help) {
            openNotification();
        }
    }
    render() { 
        const { 
            user, help, inverseProduit, userUid, newUserUid, choixUserUid, linkName, followWindow,
                toggleSwitchHelp, toggleSwitchInverseProduit 
            } = this.props;
        return ( 
            <Col span={24}>
                <Row type="flex" justify="center">
                    <Col  xs={{ span: 24 }} md={{ span: 22 }} lg={{ span: 20 }}>
                    <h2>Bienvenue sur votre profil {user}</h2>
                    </Col>
                </Row>
                <Row type="flex" justify="center">
                    <Col xs={{ span: 24 }} md={{ span: 22 }} lg={{ span: 20 }} style={{ marginBottom : '1em'}}>
                        <span><b>Aide : </b></span>
                        <Switch checkedChildren="oui" unCheckedChildren="non" checked={help === true ? true : false} onChange={toggleSwitchHelp} id='switchButtonHelp'/>
                    </Col>
                    <Col xs={{ span: 24 }} md={{ span: 22 }} lg={{ span: 20 }} style={{ marginBottom : '1em'}}>
                        <span><b>Inverser l'ordre des produits : </b></span>
                        <Switch checkedChildren="oui" unCheckedChildren="non" checked={inverseProduit === true ? true : false} onChange={toggleSwitchInverseProduit} id='switchButtonInverseProduit'/>
                    </Col>
                    <Col xs={{ span: 24 }} md={{ span: 22 }} lg={{ span: 20 }} style={{ marginBottom : '1em'}}>
                        <span><b>Case d'ajout de produit qui suit l'écran : </b></span>
                        <Switch checkedChildren="oui" unCheckedChildren="non" checked={followWindow === true ? true : false} onChange={this.props.toggleSwitchFollowWindow} id='switchButtonInverseProduit'/>
                    </Col>
                    <Col xs={{ span: 24 }} md={{ span: 22 }} lg={{ span: 20 }} style={{ marginBottom : '1em'}}>
                        <span><b>Votre identifiant : </b>{userUid}</span>
                    </Col>
                    <Col xs={{ span: 24 }} md={{ span: 22 }} lg={{ span: 20 }}>
                        <Button block type={choixUserUid ? "primary" : "default"} name={userUid}  onClick={!choixUserUid ? this.props.choisirUserUid : null} className='choisirUid'>
                        {choixUserUid ? <span>Votre liste</span>: <span>Rejoindre votre liste</span>}
                        </Button>
                    </Col>
                    <Col xs={{ span: 24 }} md={{ span: 22 }} lg={{ span: 20 }} style={{ marginBottom : '1em'}}>
                        <Button block type={choixUserUid ? "default" : "primary"} name={newUserUid} onClick={choixUserUid ? this.props.choisirUserUid : null} className='choisirUid'>
                            {choixUserUid ? <span>Rejoindre la liste de {linkName}</span>: <span>Liste de {linkName}</span>}
                        </Button>
                    </Col>
                    <Col xs={{ span: 24 }} md={{ span: 22 }} lg={{ span: 20 }} style={{ marginBottom : '1em'}}>
                        <span><b>Ajouter un identifiant : </b></span>
                        <input type='text' placeholder='Entrez le nouvel identifiant' onChange={this.props.changeUserUid} required={true}/>
                        <Button type="primary" onClick={this.props.valideChangeUserUid} className='changeUid'>
                            <Icon type="check" />Valider
                        </Button>
                    </Col>
                    <Col xs={{ span: 3, pull: 10 }} md={{ span: 3, pull: 10 }} lg={{ span: 3, pull: 10 }}>
                        <Button type='primary' onClick={() => this.props.deconnexion()} style={{ borderRadius: '50px', width: '35px', height: '35px', marginLeft: '10px' }}>
                            <Icon type="logout" />
                        </Button>
                    </Col>
                </Row>
                <Row type='flex' justify='left'>
                    <Col span={6}>
                    <div className='button-anth'>
                        <Button block type="primary" onClick={this.props.returnToApp} className='goBack'>
                            <Icon type="left" />Retour
                        </Button>
                    </div>
                    </Col>
                </Row>
            </Col>
         );
    }
}
 
export default ProfilUser;