import React, { Component } from 'react';

import { Col } from 'antd';

class EnCourseItem extends Component {
    render() { 
        const { item, choixListe } = this.props;
        return ( 
            <Col span={24} key={`key-${item}`} className='listeDeBaseEnCourse'>  
                <Col xs={{ span: 20 }} md={{ span: 21 }} lg={{ span: 22 }} key={`elt-${item}`} id={`id-${item}`} onClick={choixListe}>
                    {item}
                </Col>
            </Col>
         );
    }
}
 
export default EnCourseItem;