import React, { Component } from 'react';

import { Button, Icon, Col, notification, Switch } from 'antd';

import ItemList from './ItemList';

const openNotification = () => {
    notification.open({
        message: 'Ajouter une catégorie',
        description: "Pour ajouter une catégorie, cliquez sur \"Ajouter une catégorie\" et écrivez le nom que vous désirez, par exemple \"Boissons\". Pour finaliser, cliquez sur le +.",
        duration: 0,
        style: {
            textAlign: 'justify'
          },
        onClose: () => {
            notification.open({
                message: 'Ajouter un produit',
                description: "Pour ajouter un produit, cliquez sur \"Ajouter un produit\" et écrivez le nom que vous désirez, par exemple \"Eau\". Pour finaliser, cliquez sur le +. Vous pouvez ajouter autant de produits que vous le désirez.",
                duration: 0,
                style: {
                    textAlign: 'justify'
                  },
                  onClose: () => {
                    notification.open({
                        message: 'Astuce!',
                        description: "Pour ajouter un produit dans une catégorie existente, cliquez sur le nom de la catégorie voulue, ensuite il vous suffit d'ajouter un produit.",
                        duration: 0,
                        style: {
                            textAlign: 'justify'
                          }
                      });
                  }
              });
        },
      });
  };

class MyLists extends Component {
    componentDidMount() {
        if(this.props.help) {
            openNotification();
        }
        window.addEventListener('scroll', this.props.handleScroll);
    }

    componentWillUnmount() {
        window.removeEventListener('scroll', this.props.handleScroll);
    }
    
    render() { 
        const { isCategory, showForm, currentCategory, currentTitre } = this.props;
        const positionY = window.scrollY;
        return ( 
            <Col onScroll={e => this.props.handleScroll(e)} className='containerList'>
                    <Col xs={{ span: 24 }} md={{ span: 22, push: 1 }} lg={{ span: 20, push: 2 }} id='containerForm'>
                        <>
                            <Col xs={{ span: 12}} sm={{ span: 5}} md={{ span: 4}} lg={{ span: 6}}>
                                <h3 className='titreListe'><i className="far fa-list-alt" style={{ color: '#078073', padding: '0.5em'}}></i>{currentTitre}</h3>
                            </Col>
                            <Col xs={{ span: 6}} sm={{ span: 5}} md={{ span: 4}} lg={{ span: 6}}>
                                <Button block type="primary" className='reload' onClick={this.props.reloadList}>
                                    <Icon type="reload" />
                                </Button>
                            </Col>
                        </>
                    </Col>
                    <Col span={24}>
                        <ItemList
                            data={this.props.data}
                            isChecked={this.props.isChecked}
                            delete={this.props.delete}
                            toggleComment={this.props.toggleComment}
                            addComment={this.props.addComment}
                            titre={this.props.titreListe}
                            indexItem={this.props.indexItem}
                            currentCategory={this.props.currentCategory}
                            numChildCategory={this.props.numChildCategory}
                            deleteCategory={this.props.deleteCategory}
                            currentTitre={this.props.currentTitre}
                            getCurrentCategory={this.props.getCurrentCategory}
                            inverseProduit={this.props.inverseProduit}
                            visible={this.props.visible}
                            onClose={this.props.onClose}
                            item={this.props.item}
                            modalContent={this.props.modalContent}
                            largeur={this.props.largeur}
                            itemIsVisible={this.props.itemIsVisible}
                            itemVisible={this.props.itemVisible}
                            categoryVisible={this.props.categoryVisible}
                            categoryIsVisible={this.props.categoryIsVisible}
                            createNewItem={this.props.createNewItem}
                            inputOpen={this.props.inputOpen}
                            openInput={this.props.openInput}
                            currentCategory={this.props.currentCategory}
                            showForm={this.props.showForm}
                            createNewCategory={this.props.createNewCategory}
                            showFormulaire={this.props.showFormulaire}
                        />
                    </Col>
                <Col span={6} style={{ marginRight: '1em' }}>
                    <Button block type="primary" onClick={this.props.goHome} className='goBack'>
                        <Icon type="left" />Retour
                    </Button>
                </Col>
            </Col>
         );
    }
}
 
export default MyLists;