import React, { Component } from 'react';
import { Button, Icon, Col, List } from 'antd';

import EnCourseList from './EnCourseList';
import EnCourseItem from './EnCourseItem';

class EnCourse extends Component {
    liste = () => {
        const { data, choixUserUid } = this.props;
        const newData = [];
        Object.keys(data).map(item => item !== 'user' ? newData.push(item) : null); //on enlève user des items
        return ( 
            <List
                bordered
                locale={{ emptyText: 'Vous devez d\'abord créer une liste avant de faire vos courses' }}
                dataSource={newData}
                renderItem={ (item, id) => (
                    <>
                    {
                    item ?
                    <>
                    {
                        choixUserUid ?
                        <List.Item>
                            <EnCourseItem
                                item={item}
                                choixListe={() => this.props.choixListe(item)}
                            />
                        </List.Item>
                        :
                        <>
                        {
                            data[item].locked === false ?
                            <List.Item >
                                <EnCourseItem
                                    item={item}
                                    choixListe={() => this.props.choixListe(item)}
                                />
                            </List.Item>
                            :
                            null
                        }
                        </>

                    }
                    </>
                    :
                    null
                    }
                    </>
                )}
            />
        );
    }
    render() { 
        return ( 
            <>
            {
            !this.props.clicked ?
            <>
                <Col xs={{ span: 24 }} md={{ span: 22, push: 1 }} lg={{ span: 20, push: 2 }}>
                    {this.liste()}
                </Col>
                <Col span={6} style={{ marginRight: '1em' }}>
                    <Button block type="primary" onClick={this.props.returnToApp} className='goBack'>
                        <Icon type="left" />Retour
                    </Button>
                </Col>
                </>
                :
                <Col span={24}>
                    <EnCourseList
                        currentTitre={this.props.currentTitre}
                        createNewItemLastMinute={this.props.createNewItemLastMinute}
                        data={this.props.data}
                        isChecked={this.props.isChecked}
                        delete={this.props.delete}
                        toggleComment={this.props.toggleComment}
                        addComment={this.props.addComment}
                        titreListe={this.props.titreListe}
                        returnToApp={this.props.returnToApp}
                        closeComment={this.props.closeComment}
                        goHome={this.props.goHome}
                        indexItem={this.props.indexItem}
                        checkCurrentCategory={this.props.checkCurrentCategory}
                        help={this.props.help}
                        inverseProduit={this.props.inverseProduit}
                        visible={this.props.visible}
                        onClose={this.props.onClose}
                        item={this.props.item}
                        largeur={this.props.largeur}
                        modalContentCourse={this.props.modalContentCourse}
                        showFormulaire={this.props.showFormulaire}
                        showForm={this.props.showForm}
                        handleScroll={this.props.handleScroll}
                        handleScrollButton={this.props.handleScrollButton}
                    />
                </Col>
            }
            
            </>
         );
    }
}
 
export default EnCourse;