import React, { Component } from 'react';
// import './App.css';
import './NewApp.css';
import 'antd/dist/antd.css';
import './animations.css';

import * as firebase from 'firebase';
import config from './firebase';

//ANT design
import { 
  Row, Col, Layout, Icon, message, Input, Slider, InputNumber, Select, Card, Avatar, Skeleton 
} from 'antd';

import Home from './Components/Home';
import Loading from './Components/Loading';
import EnCourse from './Components/EnCourse';
import Menu from './Components/Menu';
import ProfilUser from './Components/ProfilUser';
import HeaderMenu from './Components/HeaderMenu';

import StyledFirebaseAuth from 'react-firebaseui/StyledFirebaseAuth';

import prochainement from './img/prochainement2.png';

const {
  Header, Footer, Content,
} = Layout;
const { Meta } = Card;

const {TextArea} = Input;
const Option = Select.Option;

class App extends Component {
  constructor(props) {
    super(props);
    firebase.initializeApp(config);
        this.state = {
            listeDeBase: [],
            data: '',
            item: null,
            titreListe: [],
            currentTitre: '',
            choixComponent: 'menu',
            currentCategory: '' || 'Ma super catégorie',
            numChildCategory: 0,
            isSignedIn: false,
            clicked: false, 
            loading: true,
            goHome: false,
            isCategory: true,
            clickedText : false,
            indexItem: null,
            isToggleSwitchType: false,
            help: true,
            inverseProduit: true,
            userId: '' || 'Mikael',
            userUid: '',
            newUserUid: '',
            choixUserUid: true,
            changeUserInput: false,
            currentUserUid: '',
            linkName: '',
            followers: [],
            count: 0,
            countItem: 0,
            typeCount: 'unité',
            newComment: '',
            showForm: false, //affiche ou non le form dans faire ses courses
            inputOpen: false, // rend visible l'input du form
            visible: false, //affiche le drawer
            itemIsVisible: true, //affiche ou non l'item dans la liste de course
            categoryIsVisible: true, //affiche ou non la catégorie dans la liste de course
            totalItem: 0,
            isScroll: 0,
            followWindow: true,
            largeur: 0,
            hauteur: 0,
            lastMinuteItem: false //verifie si un item a ete ajoute a la derniere minute
        };
      this.delete = this.delete.bind(this);
      this.deleteCategory = this.deleteCategory.bind(this);
      this.createNewItem = this.createNewItem.bind(this);
      this.addNewListTitre = this.addNewListTitre.bind(this);
      this.deconnexion = this.deconnexion.bind(this);
}

uiConfig = {
  // Popup signin flow rather than redirect flow.
  signInFlow: 'popup',
  // We will display Google and Facebook as auth providers.
  signInOptions: [
      firebase.auth.EmailAuthProvider.PROVIDER_ID,
      firebase.auth.GoogleAuthProvider.PROVIDER_ID,
      firebase.auth.FacebookAuthProvider.PROVIDER_ID,
  ],
  callbacks: {
    // Avoid redirects after sign-in.
    signInSuccessWithAuthResult: () => false
  }
};

componentDidMount() {
  const { loading } = this.state;
    this.unregisterAuthObserver = firebase.auth().onAuthStateChanged(
      (user) => {
        if(user) {
          if(loading) {
            this.getDataBase(user.uid);
          }
          this.setState({
            isSignedIn: !!user, 
            userId: user.displayName,
            userUid: user.uid
          });
        } else {
          return null
        }
      }
    );
  
  //détection du changement de taille d'écran
  this.handleResize();
  window.addEventListener('resize', this.handleResize);
}

componentWillUnmount() {
  window.removeEventListener('resize', this.handleResize); //on enlève l'évènement qui regarde la taille de la fenêtre
  this.unregisterAuthObserver();
}

  animFormButton = button => {
    button.classList.add('formButton-active');
    setTimeout(() => {
        button.classList.remove('formButton-active');
    }, 500);
  }

  animFormElt = elt => {
    elt.classList.add('message-enter');
  }

  getDataBase = myUser => {
    const { userId, choixUserUid, newUserUid } = this.state;
    const userUid = myUser || this.state.userUid;
    const currentUser = choixUserUid ? userUid : newUserUid;
    const ref = firebase.database().ref(`${currentUser}`);
        ref.on('value', snapshot => {
          const listItem = [];
          if(snapshot.val() !== null) {
            firebase.database().ref(`${userUid}/user/`).on('value', snapshot => {
              this.setState({ 
                help: snapshot.val().help,
                inverseProduit: snapshot.val().inverseProduit,
                userUid: snapshot.val().userUid,
                newUserUid: snapshot.val().newUserUid,
                choixUserUid: snapshot.val().choixUserUid,
                currentUserUid: snapshot.val().choixUserUid === true ? snapshot.val().userUid : snapshot.val().newUserUid,
                linkName: snapshot.val().linkName,
                followers: snapshot.val().followers,
                followWindow: snapshot.val().followWindow
               });
               if(snapshot.val().choixUserUid) {
                firebase.database().ref(`${currentUser}`).on('value', snapshot => {
                  Object.keys(snapshot.val()).map(item =>
                    listItem.push(item)
                  );
                  this.setState({
                    listeDeBase: listItem,
                    data: snapshot.val(),
                    loading: false
                    });
                });
               } else {
                const newUserUid = snapshot.val().newUserUid;
                firebase.database().ref(`${newUserUid}`).on('value', snapshot => {
                  Object.keys(snapshot.val()).map(item =>
                    listItem.push(item)
                  );
                  this.setState({
                    listeDeBase: listItem,
                    data: snapshot.val(),
                    loading: false
                    });
                });
               }
            });
            
            } else {
              firebase.database().ref(`${userUid}`).set({
                user: {
                  userName: userId,
                  userUid: userUid,
                  newUserUid: '',
                  help: true,
                  inverseProduit: true,
                  choixUserUid: true,
                  linkName: '',
                  followers: [],
                  followWindow: true
                }
              });
              this.setState({
                loading: false
            });
          }
        });
  }

  //charger la database du compte sélectionné
  getOtherDataBase = (linkName) => {
    const curentUser = this.state.currentUserUid;
    const user = this.state.userId;
    const ref = firebase.database().ref(`${curentUser}`);
        ref.on('value', snapshot => {
          const listItem = [];
          if(snapshot.val() !== null) {
            firebase.database().ref(`${curentUser}/user`).on('value', snapshot => {
              this.setState({ 
                linkName: snapshot.val().userName === user ? linkName : snapshot.val().userName  //probleme quand retour sur user d'origine
               });
            });
            firebase.database().ref(`${curentUser}`).on('value', snapshot => {
              Object.keys(snapshot.val()).map(item =>
                listItem.push(item)
              );
                this.setState({
                  listeDeBase: listItem,
                  data: snapshot.val(),
                  loading: false
                  });
              });
            }
        });
        
  }

  showFormulaire = () => {
    const showForm = !this.state.showForm;
    
    setTimeout(() => {
      this.setState({ showForm });
    }, 300);
  }

  //détecter la taille de la fenêtre
  handleResize = () => this.setState({
    hauteur: window.innerHeight,
    largeur: window.innerWidth
  });

  // detection du scroll
  handleScroll = () => {
    const { followWindow, showForm } = this.state;
    let isScroll = 0;
    const formElt = showForm ? document.getElementById('myForm2') : document.getElementById('buttonSubmit2');
    let lastPositionY = window.scrollY;
    if(formElt && followWindow) {
      if(isScroll !== lastPositionY && lastPositionY > 80) {
        formElt.classList.add('opacity');
        if(lastPositionY > 80) {
          formElt.classList.add('fixedPosition');
        }
        setTimeout(() => {
          isScroll = lastPositionY;
          formElt.classList.remove('opacity');
        }, 600);
      }
      if(lastPositionY <= 80) {
        formElt.classList.remove('fixedPosition');
      }
    }
}

  // on ou off de l'aide
  toggleSwitchHelp = checked => {
    const { userUid } = this.state;
    const help = checked;
    this.setState({ help });
    let updates = {};
    updates[`${userUid}/user/help`] = !help;
    return firebase.database().ref().update(updates);
  }

  // on ou off pour inverser l'ordre des produits
  toggleSwitchInverseProduit = checked => {
    const { userUid } = this.state;
    const inverseProduit = checked;
    this.setState({ inverseProduit });
    let updates = {};
    updates[`${userUid}/user/inverseProduit`] = !inverseProduit;
    return firebase.database().ref().update(updates);
  }

  toggleSwitchFollowWindow = checked => {
    // const user = this.state.userUid;
    const followWindow = checked;
    this.setState({ followWindow });
    // let updates = {};
    // updates[`${user}/user/followWindow`] = !followWindow;
    // return firebase.database().ref().update(updates);
  }

  // changer l'uid pour rejoindre une liste
  changeUserUid = event => {
    const newUserUid = event.target.value;
    if(newUserUid.length > 20) {
      this.setState({ 
        newUserUid, 
        changeUserInput: true 
      });
    }
  }

  error = text => {
    message.error(`${text}`);
  };

  //valider le changement de l'uid
  valideChangeUserUid = () => {
    const { newUserUid, changeUserInput, userUid, userId } = this.state;
    const followers = [];
    const message = 'Utilisateur trouvé!'
    //on rajoute le nom du followers
    followers.push(userId);
    let updates = {};
    if(newUserUid !== '' && changeUserInput) {
      firebase.database().ref(`${newUserUid}/user/userName`).on('value', snapshot => {
        if(snapshot.val()) {
          this.setState({ 
            linkName: snapshot.val(),
            followers,
            changeUserInput: false
          });
          updates[`${userUid}/user/linkName`] = snapshot.val();
          firebase.database().ref().update(updates);

          updates[`${userUid}/user/newUserUid`] = newUserUid;
          firebase.database().ref().update(updates);
      
          updates[`${newUserUid}/user/followers`] = followers;
          firebase.database().ref().update(updates);

          this.success(message);
        } else {
          this.error('Cet utilisateur n\'existe pas');
          return;
        }
      });
    } else {
      this.error('Erreur de saisie');
    }
  }

  choisirUserUid = event => {
    const { choixUserUid, userUid, linkName} = this.state;
    this.setState({ 
      choixUserUid: !choixUserUid,
      currentUserUid: event.target.name
    });

    const choixDB = !choixUserUid;
    if(choixDB) {
        this.getDataBase();
    } else {
        this.getOtherDataBase(linkName);
    }

    this.success('Liste chargée!');
        this.setState({ 
          currentTitre: '',
          clicked: false,
          choixComponent: 'menu'
        });

    let updates = {};
    updates[`${userUid}/user/choixUserUid`] = !choixUserUid;
    firebase.database().ref().update(updates);
  }

  // compteur de produits dans une liste
  countProduit = arrayData => {
    let countItemPos = [];
    let countItemNeg = [];
    arrayData.map(elt => Object.keys(elt.categoryItems)
      .map(item => elt.categoryItems[item].checked === false ? 
          countItemNeg.push(elt.categoryItems[item].checked)
          : 
          countItemPos.push(elt.categoryItems[item].checked)));
    this.setState({
        countItem : countItemNeg.length - countItemPos.length
    });
}

  // Regarde si un produit est checked
  isChecked = item => {
    const { currentUserUid, currentTitre, data } = this.state;
    const collapse = document.querySelector(`#elt-${item.key}`);
    const divElt = collapse.parentElement.parentElement.parentElement;
    divElt.classList.toggle('flip');
    const currentCategory = collapse.parentElement.parentElement.parentElement.id;
    item.checked = !item.checked;

    this.setState({ data });

    let index = '';
    firebase.database().ref(`${currentUserUid}/${currentTitre}/category`).on('value', snapshot => {
      snapshot.forEach(function(childSnapshot){
          return index = childSnapshot.val().titreCat === currentCategory ? childSnapshot.key : null;
      });
    });

  let itemKey = {};
  firebase.database().ref(`${currentUserUid}/${currentTitre}/category/${index}/categoryItems`).orderByChild('key').equalTo(item.key).on('value', snapshot => {
    snapshot.forEach(function(childSnapshot){
      return itemKey = childSnapshot.key;
    });
  });

  let updates = {};
  updates[`/${currentUserUid}/${currentTitre}/category/${index}/categoryItems/${itemKey}`] = item;

  return firebase.database().ref().update(updates);
}
  // Delete un produit
  delete = item => {
    const { currentUserUid, currentTitre } = this.state;
    const collapse = document.querySelector(`#elt-${item.key}`);
    const currentCategory = collapse.parentElement.parentElement.parentElement.id;
    const contentElt = collapse.parentElement.parentElement.parentElement.parentElement.childNodes;
    
    this.setState({ 
      indexItem: `elt-${item.item}`,
      numChildCategory: contentElt.length
  });

    let index = '';
    firebase.database().ref(`${currentUserUid}/${currentTitre}/category`).on('value', snapshot => {
      snapshot.forEach(function(childSnapshot){
          return index = childSnapshot.val().titreCat === currentCategory ? childSnapshot.key : null;
      });
    });
    if(contentElt.length === 1) {
      firebase.database().ref(`${currentUserUid}/${currentTitre}/category/${index}`).orderByChild('key').equalTo(item.key).on('value', snapshot => {
        const indexCat = snapshot.key;
          firebase.database().ref(`${currentUserUid}/${currentTitre}/category/${indexCat}`).remove();
      });
      setTimeout(() => {
        this.setState({ indexItem: null});
      }, 50);
    } else {
        firebase.database().ref(`${currentUserUid}/${currentTitre}/category/${index}/categoryItems`).orderByChild('key').equalTo(item.key).on('value', snapshot => {
          snapshot.forEach(function(childSnapshot){
            const indexItem = childSnapshot.key;
              firebase.database().ref(`${currentUserUid}/${currentTitre}/category/${index}/categoryItems/${indexItem}`).remove();
          });
        });
        setTimeout(() => {
          this.setState({ indexItem: null});
        }, 50);
    }
  }

  // delete une catégorie
  deleteCategory = (item, id) => {
    const user = this.state.currentUserUid;
    const collapse = document.getElementById(item.titreCat);
    const currentTitre = this.state.currentTitre;

    let index = '';
    firebase.database().ref(`${user}/${currentTitre}/category`).on('value', snapshot => {
      snapshot.forEach(function(childSnapshot){
          return index = childSnapshot.val().titreCat === collapse.id ? childSnapshot.key : null;
      });
    });

    firebase.database().ref(`${user}/${currentTitre}/category/${index}`).remove();
    
    setTimeout(() => {
      this.setState({ indexItem: null});
    }, 50);
  }

  deleteList = (item) => {
    const { currentUserUid } = this.state;
    this.setState({ indexItem: `elt-${item}` });

    firebase.database().ref(`${currentUserUid}/${item}`).remove();

    setTimeout(() => {
      this.setState({ indexItem: null });
    }, 50);
    
  }

  confirmDelete = item => {
    this.setState({ clicked: false });
    this.deleteList(item);
    message.success('Liste effacée');
  }

  cancel = e => {
      message.error('Action annulée');
    }

  onChangeComment = (event, item) => {
    const newComment = event.target.value;
    item.comment = newComment;
    this.setState({
      item,
      newComment
    });
  }

  onChangeCount = (value, item) => {
    const count = value;
    item.count = value;
    this.setState({
      count,
      item
    });
  }

  onChangeTypeCount = (value, item) => {
    const typeCount = value;
    item.typeCount = value;
    this.setState({
      typeCount,
      item
    });
  }

  modalContent = item => {
    const { count, largeur, typeCount } = this.state;
    return (
      <>
        <Row gutter={largeur > 720 ? 12 : 8}>
          <Col span={ largeur > 720 ? 14 : 24 }>
            <Slider
              min={0}
              max={typeCount === 'Gramme' || typeCount === 'ml' || typeCount === 'cl' ? 1000 : 25 }
              onChange={e => this.onChangeCount(e, item)}
              value={typeof count === 'number' ? count : 0}
            />
          </Col>
          <Col span={ largeur > 720 ? 5 : 12 }>
            <InputNumber
              min={0}
              max={typeCount === 'Gramme' || typeCount === 'ml' || typeCount === 'cl' ? 1000 : 25 }
              style={{ marginLeft: 16, width: '90%' }}
              value={count}
              onChange={e => this.onChangeCount(e, item)}
            />
          </Col>
          <Col span={ largeur > 720 ? 5 : 12 }>
            <Select defaultValue={typeCount} onChange={e => this.onChangeTypeCount(e, item)} style={{ width: '90%' }}>
              <Option value='unité'>{count <= 1 ? 'unité' : 'unités'}</Option>
              <Option value='boite'>{count <= 1 ? 'boite' : 'boites'}</Option>
              <Option value='litre'>{count <= 1 ? 'litre' : 'litres'}</Option>
              <Option value='ml'>ml</Option>
              <Option value='cl'>cl</Option>
              <Option value='Kilo'>{count <= 1 ? 'kilo' : 'kilos'}</Option>
              <Option value='Gramme'>{count <= 1 ? 'gramme' : 'grammes'}</Option>
            </Select>
          </Col>
        </Row>
        <TextArea 
          placeholder='ajouter un commentaire' 
          autosize={{ minRows: 5, maxRows: 10 }} 
          onChange={e => this.onChangeComment(e, item)}
          value={item.comment} 
          rows={10}
          cols={largeur > 720 ? 50 : 30}
          autoFocus
          autoComplete='on'
          style={{ marginTop: '1em' }}
          onPressEnter={event => this.onClose(event, item)}
        />
      </>
    )
  }

  modalContentCourse = item => {
    const { count, largeur } = this.state;
    const image = (
        <Skeleton paragraph={{ rows: 4, width: ['90%', '90%','90%','90%'] }} title={false}>
          <img alt="prochainement" src={prochainement} />
        </Skeleton>
    )
    return(
        <Card
          style={{ width: largeur > 720 ? '70%' : '90%', paddingTop: '1em'}}
          cover={image}
        >
          <Meta
            avatar={<Avatar style={{ backgroundColor: '#009688' }} icon="user" />}
            title={item.item}
          />
            <br/>
            {
              count ?
              <>
              <h4>Quantité</h4>
              <p>{item.count} {item.typeCount}{count <= 1 ? '': 's'}</p>
              </>
              :
              null
            }
            {
              item.comment ?
              <>
                <h4>Commentaire</h4>
                <p>{item.comment}</p>
              </>
              :
              <>
                <h4>Commentaire</h4>
                <p>Rien ici!</p>
              </>
            }
        </Card>
    )
  }

  //faire apparaitre/disparaitre la commentaire
  toggleComment = item => {
    this.setState({ 
      visible: true,
      item,
      count: item.count,
      typeCount: item.typeCount,
    });
  }

  onClose = (event, item) => {
    this.addComment(event, item);
    this.setState({
      visible: false,
      count: 0,
      typeCount: 'unité',
    });
  };

  // Ajouter un commentaire à un élément de la liste
  addComment = (event, items) => {
    event.preventDefault();
    const { currentUserUid, item, currentTitre } = this.state;
    const collapse = document.querySelector(`#elt-${items.key}`);
    const currentCategory = collapse.parentElement.parentElement.parentElement.id;

    let index = '';
    firebase.database().ref(`${currentUserUid}/${currentTitre}/category`).on('value', snapshot => {
      snapshot.forEach(function(childSnapshot){
          return index = childSnapshot.val().titreCat === currentCategory ? childSnapshot.key : null;
      });
    });

  let itemKey = {};
  firebase.database().ref(`${currentUserUid}/${currentTitre}/category/${index}/categoryItems`).orderByChild('key').equalTo(items.key).on('value', snapshot => {
    snapshot.forEach(function(childSnapshot){
      return itemKey = childSnapshot.key;
    });
  });

  let updates = {};
  updates[`/${currentUserUid}/${currentTitre}/category/${index}/categoryItems/${itemKey}`] = item;

  return firebase.database().ref().update(updates);
  }

  //remettre la liste a zéro (check)
  reloadList = () => {
    const { currentTitre, data, currentUserUid } = this.state;
    const arrayData = [];
    Object.keys(data).map(item => item === currentTitre ? Object.keys(data[item]).map(category => Object.keys(data[item][category]).map(categoryItem => arrayData.push(data[item][category][categoryItem]))): null);
    arrayData.map(elt => Object.keys(elt.categoryItems)
      .map(item => elt.categoryItems[item].checked === true ? 
        elt.categoryItems[item].checked = false : null));

    this.setState({ data });

    let updates = {};
    updates[`/${currentUserUid}`] = data;
  
    return firebase.database().ref().update(updates);
  }

  //rendre une liste privée
  changeLocked = (item, title) => {
    const { currentUserUid } = this.state;
    item.locked = !item.locked;

    let updates = {};
    updates[`/${currentUserUid}/${title}/`] = item;      
    return firebase.database().ref().update(updates);
  }

  //cache ou non une catégorie
  categoryVisible = elt => {
  const { currentUserUid, currentTitre } = this.state;
  const currentCategory = elt.titreCat;

  elt.categoryIsVisible = !elt.categoryIsVisible;

  let index = '';
    firebase.database().ref(`${currentUserUid}/${currentTitre}/category`).on('value', snapshot => {
      snapshot.forEach(function(childSnapshot){
          return index = childSnapshot.val().titreCat === currentCategory ? childSnapshot.key : null;
      });
    });
          
  let updates = {};
  updates[`/${currentUserUid}/${currentTitre}/category/${index}/`] = elt;
          
  return firebase.database().ref().update(updates);
  }

  itemVisible = item => {
    const { currentUserUid, currentTitre } = this.state;
    const collapse = document.querySelector(`#elt-${item.key}`);
    const currentCategory = collapse.parentElement.parentElement.parentElement.id;

    item.itemIsVisible = !item.itemIsVisible;

    let index = '';
    firebase.database().ref(`${currentUserUid}/${currentTitre}/category`).on('value', snapshot => {
      snapshot.forEach(function(childSnapshot){
          return index = childSnapshot.val().titreCat === currentCategory ? childSnapshot.key : null;
      });
    });
          
  let itemKey = {};
  firebase.database().ref(`${currentUserUid}/${currentTitre}/category/${index}/categoryItems`).orderByChild('key').equalTo(item.key).on('value', snapshot => {
    snapshot.forEach(function(childSnapshot){
      return itemKey = childSnapshot.key;
    });
  });
          
  let updates = {};
  updates[`/${currentUserUid}/${currentTitre}/category/${index}/categoryItems/${itemKey}`] = item;
          
  return firebase.database().ref().update(updates);
  }

  // Créer une catégorie dans la liste de course
  createNewCategory = event => {
    event.preventDefault();
    const { currentUserUid, currentTitre, currentCategory, choixComponent } = this.state;
    const formInput = document.querySelector('#myForm>input');
    const todoFormButton = document.querySelector('#myForm>button');
    let category = '';
    if(currentCategory === 'Ajout de dernière minute' && choixComponent === 'enCourse') {
      category = 'Ajout de dernière minute';
    } else {
      const cat = formInput.value;
      category = cat.charAt(0).toUpperCase() + cat.substring(1).toLowerCase();
    }
    
    if(formInput.value === '') {
      this.setState({ 
        showForm: false
      });
      return null
    }
    //insérer une catégorie sans exemple de produit!
    const data = {
      titreCat: category,
      categoryIsVisible: true
    }
    //on regarde si la catégorie existe déjà
    let categoryExist = false;
    firebase.database().ref(`${currentUserUid}/${currentTitre}/category`).on('value', snapshot => {
      snapshot.forEach(function(childSnapshot) {
        return categoryExist = childSnapshot.val().titreCat === category ? true : false;
      });
    });
    //si la catégorie n'existe pas, on la push dans la bdd
    if(!categoryExist) {
      firebase.database().ref(`${currentUserUid}/${currentTitre}/category`).push({
        ...data
      });
      this.setState({
        currentCategory: category,
        isCategory: false,
        showForm: false
      });
      this.animFormButton(todoFormButton);
    } else { // si elle existe message d'alerte
      this.error('Cette catégorie existe déjà!');
    }
    formInput.value = '';
  }

  //message qui annonce la catégorie sélectionnée
  success = text => {
    message.config({
      top: 90,
      duration: 2
    });
    message.success(`${text}`);
  };

  //prend la catégorie choisie au clic
  getCurrentCategory = titreCat => {
    const currentCategory = titreCat;
    this.setState({ 
      currentCategory, 
      isCategory: false,
      showForm: true
     });
    this.success(titreCat);
  }

  // Bouton switch pour choisir entre produit et catégorie
  toggleSwitch = checked => {
    this.setState({ isCategory : checked === false ? false : true });
  }

  //bouton switch pour choisir entre collapse et liste
  toggleSwitchType = checked => {
    this.setState({ isToggleSwitchType : checked === false ? false : true });
}

  // on ouvre le formulaire
  openInput = titre => {
    const currentCategory = titre;
    this.setState({ 
      inputOpen: true,
      currentCategory
    });
  }

  // Créer un élément de liste de course
  createNewItem = event => {
    event.preventDefault();
    const { currentUserUid, currentCategory, currentTitre, inputOpen } = this.state;
    const timestamp = Date.now();
    const todoFormInput = document.querySelector('.form>input');
    const todoFormButton = document.querySelector('#buttonSubmit');
    const dataElt = todoFormInput.value;
    const newData = dataElt.charAt(0).toUpperCase() + dataElt.substring(1).toLowerCase();

    if(todoFormInput.value === '') {
      this.setState({ 
        inputOpen: false
      });
      return null
    }

    if(currentCategory === 'Ma super catégorie') {
      this.createNewCategory(event);
      return null;
    }

    const data = {
      item: newData,
      checked: false,
      key: timestamp,
      comment: '',
      count: 0,
      typeCount: 'unité',
      show: false,
      itemIsVisible: true
    };
    
    if(newData !== '' && currentTitre !== '') {
      let index = '';
      firebase.database().ref(`${currentUserUid}/${currentTitre}/category`).on('value', snapshot => {
        snapshot.forEach(function(childSnapshot){
            return index = childSnapshot.val().titreCat === currentCategory ? childSnapshot.key : null;
        });
      });

      firebase.database().ref(`${currentUserUid}/${currentTitre}/category/${index}/categoryItems`).push({
        ...data
      });
    }
    
    this.animFormButton(todoFormButton);
    todoFormInput.value = '';
    this.setState({ inputOpen: false });
  }

  // Ajout d'un titre de liste de course
  addNewListTitre = event => {
    event.preventDefault();
    const { currentUserUid } = this.state;
    const formInput = document.querySelector('#form-input');
    const todoFormButton = document.querySelector('#myButtonSubmit');
    const titre = formInput.value;
    if(formInput.value === '') {
      return null
    }

    this.setState(prevState => ({
      listeDeBase: [...prevState.listeDeBase, titre],
      isCategory: true
    }));

    firebase.database().ref(`${currentUserUid}/${titre}`).set({
      locked: false
    });

    this.animFormButton(todoFormButton);
    formInput.value = '';
  }

  // Détecte quelle liste à été choisie
  choixListe = title => {
    this.setState({
      currentTitre: title,
      clicked: true
    });
  }

  // En Course --------> Check toute la catégorie
  checkCurrentCategory = titreCat => {
    const titre = document.getElementById(`titreCat-${titreCat}`);
    titre.lastChild.firstChild.classList.toggle('hidden');
  }

  // EN COURSE --------> Crée un item dans la catégorie derniere minute
  createNewItemLastMinute = event => {
    event.preventDefault();
    const todoFormInput = document.querySelector('.formu>input');
    const todoFormButton = document.querySelector('#buttonSubmit');
    const newData = todoFormInput.value;
    if(!newData) {
      return
    }
    const { currentUserUid, currentCategory, currentTitre, lastMinuteItem } = this.state;
    const timestamp = Date.now();
    const data = {
      item: newData,
      checked: false,
      key: timestamp,
      comment: '',
      count: 0,
      typeCount: 'unité',
      show: false,
      itemIsVisible: true
    };
    
    //on regarde si la catégorie existe déjà pour ne pas en recréer une identique
    let hasTitreCat = '';
    firebase.database().ref(`${currentUserUid}/${currentTitre}/category`).on('value', snapshot => {
      snapshot.forEach(function(childSnapshot){
         return hasTitreCat = childSnapshot.val().titreCat === 'Ajout de dernière minute';
      });
    });

    if(currentCategory === 'Ajout de dernière minute' && !lastMinuteItem && !hasTitreCat) {
      this.createNewCategory(event);
      this.setState({ lastMinuteItem: true });
    }

    if(newData !== '' && currentTitre !== '') {
      let index = '';
      firebase.database().ref(`${currentUserUid}/${currentTitre}/category`).on('value', snapshot => {
        snapshot.forEach(function(childSnapshot){
            return index = childSnapshot.val().titreCat === currentCategory ? childSnapshot.key : null;
        });
      });

      firebase.database().ref(`${currentUserUid}/${currentTitre}/category/${index}/categoryItems`).push({
        ...data
      });
    }
    
    this.animFormButton(todoFormButton);
    todoFormInput.value = '';
    this.setState({ showForm: false });
  }

  // utilisé sur bouton pour retour en arrière
  goHome = () => {
    this.setState({ clicked: false, isCategory: true });
  }

  // retour au menu de l'accueil
  returnToApp = () => {
    this.setState({ choixComponent: 'menu' });
  }

  // détecte le composant choisi à l'accueil
  choixComponent = event => {
    const choixComponent = event.target.name;
    if(choixComponent !== '') {
      if(choixComponent === 'enCourse') {
        this.setState({ 
          choixComponent, 
          currentCategory: 'Ajout de dernière minute',
          showForm: false
        });
      } else {
        this.setState({ 
          choixComponent, 
          currentCategory: 'Ma super catégorie',
          showForm: false
        });
      }
    }
  }

  hideText = () => {
    const clickedText = this.state.clickedText;
    this.setState({ clickedText : !clickedText });
}

deconnexion = () => {
  firebase.auth().signOut().then(function() {
    this.setState({ isSignedIn: false });
  }).catch(function(error) {
    console.log(error);
  });
}

//choix composant via headermenu
handleClick = event => {
  const choixComponent = event.key;
  if(choixComponent !== '') {
    if(choixComponent === 'enCourse') {
      this.setState({ 
        choixComponent, 
        currentCategory: 'Ajout de dernière minute',
        clicked: false,
        currentTitre: '',
        showForm: false
      });
    } else {
      this.setState({ 
        choixComponent, 
        currentCategory: 'Ma super catégorie',
        clicked: false,
        currentTitre: '',
        showForm: false,
        isCategory: true
      });
    }
  }
}

  //renvoie dans le render le composent choisit
  renderChoiseConponent = () => {
    const { choixComponent } = this.state;
    switch(choixComponent) {
      case 'enCourse':
      return <EnCourse
                returnToApp={this.returnToApp}
                choixListe={this.choixListe}
                clicked={this.state.clicked}
                indexItem={this.state.indexItem}
                help={this.state.help}

                isChecked={this.isChecked}
                delete={this.delete}
                toggleComment={this.toggleComment}
                addComment={this.addComment}
                titreListe={this.state.titreListe}
                createNewItemLastMinute={this.createNewItemLastMinute}
                goHome={this.goHome}
                currentTitre={this.state.currentTitre}
                data={this.state.data}
                checkCurrentCategory={this.checkCurrentCategory}
                inverseProduit={this.state.inverseProduit}
                visible={this.state.visible}
                onClose={this.onClose}
                item={this.state.item}
                largeur={this.state.largeur}
                modalContentCourse={this.modalContentCourse}
                showFormulaire={this.showFormulaire}
                showForm={this.state.showForm}
                handleScroll={this.handleScroll}
                choixUserUid={this.state.choixUserUid}
              />
      case 'mesListes':
      return <Home
                listeDeBase={this.state.listeDeBase}
                choixListe={this.choixListe}
                addNewListTitre={this.addNewListTitre}
                deleteList={this.deleteList}
                loading={this.state.loading}
                data={this.state.data}
                returnToApp={this.returnToApp}
                clicked={this.state.clicked}
                indexItem={this.state.indexItem}
                help={this.state.help}

                isChecked={this.isChecked}
                delete={this.delete}
                toggleComment={this.toggleComment}
                addComment={this.addComment}
                titreListe={this.state.titreListe}
                createNewItem={this.createNewItem}
                goHome={this.goHome}
                createNewCategory={this.createNewCategory}
                isCategory={this.state.isCategory}
                currentCategory={this.state.currentCategory}
                toggleSwitch={this.toggleSwitch}
                numChildCategory={this.state.numChildCategory}
                deleteCategory={this.deleteCategory}
                currentTitre={this.state.currentTitre}
                getCurrentCategory={this.getCurrentCategory}
                isToggleSwitchType={this.state.isToggleSwitchType}
                toggleSwitchType={this.toggleSwitchType}
                inverseProduit={this.state.inverseProduit}
                visible={this.state.visible}
                onClose={this.onClose}
                item={this.state.item}
                modalContent={this.modalContent}
                largeur={this.state.largeur}
                itemIsVisible={this.state.itemIsVisible}
                itemVisible={this.itemVisible}
                handleScroll={this.handleScroll}
                showForm={this.state.showForm}
                showFormulaire={this.showFormulaire}
                categoryVisible={this.categoryVisible}
                categoryIsVisible={this.state.categoryIsVisible}
                reloadList={this.reloadList}
                confirmDelete={this.confirmDelete}
                cancel={this.cancel}
                changeLocked={this.changeLocked}
                choixUserUid={this.state.choixUserUid}
                inputOpen={this.state.inputOpen}
                openInput={this.openInput}
              />
      case 'monProfil':
      return <ProfilUser
              returnToApp={this.returnToApp}
              user={this.state.userId}
              toggleSwitchHelp={this.toggleSwitchHelp}
              help={this.state.help}
              toggleSwitchInverseProduit={this.toggleSwitchInverseProduit}
              inverseProduit={this.state.inverseProduit}
              userUid={this.state.userUid}
              changeUserUid={this.changeUserUid}
              valideChangeUserUid={this.valideChangeUserUid}
              newUserUid={this.state.newUserUid}
              choixUserUid={this.state.choixUserUid}
              choisirUserUid={this.choisirUserUid}
              linkName={this.state.linkName}
              followWindow={this.state.followWindow}
              toggleSwitchFollowWindow={this.toggleSwitchFollowWindow}
              deconnexion={this.deconnexion}
              />
      default:
      return null
    }
  }

  render() {
    if (!this.state.isSignedIn) {
      return (
        <Row type="flex" justify="center">
          <Col xs={{ span: 16 }} md={{ span: 14}} lg={{ span: 10 }} id='connexion'>
            <h1>Check-Course</h1>
            <p>Veuillez vous connecter svp:</p>
            <StyledFirebaseAuth uiConfig={this.uiConfig} firebaseAuth={firebase.auth()} />
          </Col>
        </Row>
      );
    }
    if(this.state.loading) {
      return (
        <Row type="flex" justify="center">
          <Col xs={{ span: 16 }} md={{ span: 14 }} lg={{ span: 10}} id='load'>
            <Loading/>
            <h3>Chargement du caddie</h3>
          </Col>
        </Row>
      )
    }
    
    return (
        <Row type="flex" justify="space-around" id='main'>
          <Col xs={{ span: 24 }} md={{ span: 22 }} lg={{ span: 20 }} id='mainCol' >
          <Layout>
            <Header 
              style={{ 
                padding : this.state.largeur >= 480 ? '0 50px' : '0'
              }}>
              <Col xs={0} lg={4}>
                {
                  this.state.largeur >= 480 ? 
                  <h4><Icon type="shopping-cart" />Check-Course</h4>
                  :
                  null
                }
              </Col>
              <Col xs={24} lg={20} 
                style={{
                  textAlign : this.state.largeur >= 480 ? 'right' : 'center',
                  fontSize : this.state.largeur >= 480 ? '0.9em' : '0.7em'
                }}
              >
                  <HeaderMenu
                    handleClick={this.handleClick}
                    choixComponent={this.choixComponent}
                    choixComponentState={this.state.choixComponent}
                  />
              </Col>
            </Header>
          <Content>
            
            {
              this.state.choixComponent === 'menu' ?
              <>
                <Col span={24} id={this.state.largeur >= 480 ? 'bckLogo' : 'bckLogo2'}>
                  <Icon type="shopping-cart" />
                  <h2>CHECK-COURSE</h2>
                </Col>
                <Menu
                  choixComponent={this.choixComponent}
                  user={this.state.userId}
                  hideText={this.hideText}
                  clickedText={this.state.clickedText}
                  help={this.state.help}
                />
              </>
              :
              this.renderChoiseConponent()
            }
            </Content>
              <Footer>
                <p>Created By Mikael</p>
              </Footer>
            </Layout>
            </Col>
          </Row>
    );
  }
}

export default App;
