import React, { Component } from 'react';

import { Menu, Icon } from 'antd';

class HeaderMenu extends Component {
    render() { 
        return ( 
            <Menu
            onClick={this.props.handleClick}
            selectedKeys={[this.props.choixComponentState]}
            mode="horizontal"
          >
            <Menu.Item key="menu">
                <Icon type="home" />
            </Menu.Item>
            <Menu.Item key="mesListes">
                <Icon type="ordered-list" />
            </Menu.Item>
            <Menu.Item key="enCourse">
                <Icon type="shopping" />
            </Menu.Item>
            <Menu.Item key="monProfil">
                <Icon type="user" />
            </Menu.Item>
          </Menu>
        );
    }
}
 
export default HeaderMenu;