import React, { Component } from 'react';

import '../animations.css';
import '../NewApp.css';

import Item from './Item'

import { List, Row, Col, Icon, Button, Badge, Drawer } from 'antd';

class ItemList extends Component {
    constructor(props) {
        super(props);
        this.state = {
            arrayData: []
        }
    }

    componentDidMount() {
        this.renderData();
    }

    componentDidUpdate(prevProps, prevState) {
        if(prevProps.data !== this.props.data) {
            this.renderData();
        }
        if(this.nameInput) {
            this.nameInput.focus();
        }
    }

    renderData = () => {
        const { data, currentTitre, inverseProduit } = this.props;
        const arrayData = [];
        if(!data) {
            return null
        }
        Object.keys(data)
            .map(item => item === currentTitre ? 
                Object.keys(data[item])
                    .map(category => Object.keys(data[item][category])
                        .map(categoryItem => arrayData.push(data[item][category][categoryItem])))
                : null);
        if(inverseProduit) {
            arrayData.reverse();
        }
        this.setState({ arrayData });
    }

    liste = () => {
        const { arrayData } = this.state;
        const { inputOpen, currentCategory } = this.props;
        if(!arrayData) {
            return null
        }
        return (arrayData || []).map((elt, id) => 
            <List
                key={`listId-${id}`}
                id={elt.titreCat}
                locale={{ emptyText: 'Veuillez ajouter un produit' }}
                header={
                    <Row type="flex" justify="space-between" align='middle'>
                        <Col xs={16} md={18} lg={20} className='titreCat' onClick={() => this.props.getCurrentCategory(elt.titreCat)}>
                            {elt.titreCat}
                        </Col>
                        <Col xs={4} sm={3} lg={2}>
                            <Button 
                                style={{ background: 'transparent' }}
                                name="isVisible" 
                                className="trash"
                                type='primary'
                                block
                                onClick={() => this.props.categoryVisible(elt)}>
                                    {
                                        elt.categoryIsVisible ?
                                        <i className="far fa-eye" style={{ color: 'aliceblue' }}/>
                                        :
                                        <i className="far fa-eye-slash" style={{ color: 'aliceblue' }}/>
                                    }
                            </Button>
                        </Col>
                        <Col xs={4} md={3} lg={2}>
                            <Button 
                                name="trash" 
                                className="trash"
                                block
                                type='primary'
                                onClick={() => this.props.deleteCategory(elt, id)}>
                                <Icon type='delete' style={{ color : '#FFF', fontSize: '1.4em' }}/>
                            </Button>
                        </Col>
                    </Row>
                }
                footer={
                    <>
                    {
                        inputOpen && currentCategory === elt.titreCat ? 
                        <Row type="flex" justify="center" align='middle'>
                            <form className='form form-titre' id='myForm' onSubmit={ e => this.props.createNewItem(e)}>
                                <input 
                                    id='myInput' 
                                    type='text' 
                                    placeholder='Ajouter un produit'
                                    maxLength={40} 
                                    autofocus="true" 
                                    ref={(input) => { this.nameInput = input; }} 
                                />
                                <button type='submit' id='buttonSubmit'>
                                    <Icon type="plus" />
                                </button>
                            </form>
                        </Row>
                        :
                        <Row 
                            type="flex" 
                            justify="center" 
                            align='middle' 
                            onClick={() => this.props.openInput(elt.titreCat)}
                            >
                            <p>Ajouter un produit</p>
                        </Row>
                    }
                    </>
                }
                bordered
                dataSource={Object.keys(elt.categoryItems || []).map(item => elt.categoryItems[item]).reverse()}
                renderItem={(item, id) => (
                    <List.Item key={`listItemId-${id}`} id={elt.titreCat} className={item.checked ? '.ant-list-item flip' : '.ant-list-item'}>
                        <Item
                            item={item.item}
                            itemKey={item.key}
                            comment={item.comment}
                            count={item.count}
                            itemIsVisible={item.itemIsVisible}

                            isChecked={() => this.props.isChecked(item)}
                            toggleComment={() => this.props.toggleComment(item)}
                            itemVisible={() => this.props.itemVisible(item)}
                            deleteItem={() => this.props.delete(item)}
                        />
                    </List.Item>
                    )}
            />
    
        );
    }
    render() { 
        const { item, largeur, visible, showForm } = this.props;
        const drawerWidth = largeur > 720 ? '50%' : '90%';
        return ( 
            <>
            {
                showForm ?
                <form className='form form-titre' id='myForm' onSubmit={ e => this.props.createNewCategory(e)}>
                    <input 
                        id='myInput2'
                        type='text' 
                        placeholder='Ajouter une catégorie'
                        maxLength={40} 
                        autofocus="true" 
                        ref={(input) => { this.nameInput = input; }} 
                    />
                    <button type='submit' id='buttonSubmit'>
                        <Icon type="plus" />
                    </button>
                </form>
                :
                <Row 
                    className='ant-list-footer addCategory' 
                    type="flex" 
                    justify="center" 
                    align='middle'
                    onClick={this.props.showFormulaire}
                    >
                    <p>Ajouter une catégorie</p>
                </Row>
            }
            {this.liste()}
                <Drawer
                    placement="right"
                    width={drawerWidth}
                    onClose={(e) => this.props.onClose(e, item)}
                    visible={visible}
                    >
                    {
                        visible ?
                        this.props.modalContent(item)
                        :
                        null
                    }
                </Drawer>
            </>
         );
    }
}
 
export default ItemList;